html {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	font-size: 1rem;
}

h1 {
	font-size: 3em;
	line-height: 30pt;
}

p {
	font-size: 1em;
}

%component {
	width: 100%;
}

.component {
	@extend %component;

	font-size: 24px;
	color: red;

	margin: 0 1px 3REM;
	color: #AABBCC;
}
